* {
  box-sizing: border-box;
}

h1 {
  font-family: Helvetica, sans-serif;
  font-weight: bold;
  font-size: 28px;
}

.highlight-container, .highlight {
  position: relative;
}
.highlight-container {
  display: inline-block;
  &:before, &:after {
    content: ' ';
    display: block;
    height: 90%;
    width: 100%;
    margin-left: -3px;
    margin-right: -3px;
    position: absolute;
  }

  &:before {
    background: rgba(234,221,6,0.9);
    transform: rotate(2deg);
    top: -1px;
    left: -1px;
  }

  &:after {
    background: rgba(234,210,6,0.6);
    top: 3px;
    right: -2px;
  }

  .highlight {
    color: #333;
    z-index: 4;
  }

}